import { InjectionToken } from "@angular/core";
import { syncCustomer } from "../interface/syncCustomer.interface";
import { storeCustomer } from "../interface/storeCustomer.interface";
import { customerAction } from "../interface/customerAction.interface";
import { showCustomerRule } from "../interface/showCustomerRule.interface";

export const syncCustomerToken = new InjectionToken<syncCustomer>('syncCustomer');
export const storeCustomerToken = new InjectionToken<storeCustomer>("storeCustomer");
export const customerActionToken = new InjectionToken<customerAction>("customerAction");
export const showCustomerRuleToken = new InjectionToken<showCustomerRule>("showCustomerRule");
