import { StringUtils } from "@allianzSND/core";

export class CustomerAddress{

    public clientID : string;
    public addressType : string;
    public country : string;
    public city : string;
    public area : string;
    public zipcode : string;
    public address : string;
    public dataSource : string;
    

    constructor(clientID:string,addressType : string,country : string,city : string,area : string,zipcode : string,address : string,dataSorce : string){
        this.clientID = clientID;
        this.addressType = addressType;
        this.country = country;
        this.city = city;
        this.area = area;
        this.zipcode = zipcode;
        this.address = address;
        this.dataSource = dataSorce;

    }

    toFullAddress() {        
        
        let array = [];
        if(StringUtils.isNotEmpty(this.country)) array.push(this.country);
        if(StringUtils.isNotEmpty(this.city)) array.push(this.city);
        if(StringUtils.isNotEmpty(this.area)) array.push(this.area);
        if(StringUtils.isNotEmpty(this.zipcode)) array.push(this.zipcode);
        if(StringUtils.isNotEmpty(this.address)) array.push(this.address);

        return array.join(', ');
    }

    public isEmpty() {
        return StringUtils.isEmpty(this.country) 
        && StringUtils.isEmpty(this.city) && StringUtils.isEmpty(this.area)
        && StringUtils.isEmpty(this.zipcode) && StringUtils.isEmpty(this.address);
    }
}
