import { Component, OnInit,Input, Output ,EventEmitter, ChangeDetectorRef, TemplateRef } from '@angular/core';
import { DateUtils, StringUtils, Language } from '@allianzSND/core'
import { CustomerDetail } from '../../service/model/CustomerDetail'; 

@Component({
  selector: 'app-customer-detail',
  templateUrl: './customer-detail.component.html',
  styleUrls: ['./customer-detail.component.scss']
})
export class CustomerDetailComponent implements OnInit {

  @Output()
  private onEditDetail: EventEmitter<string> = new EventEmitter();
  @Output()
  private onCallPhone: EventEmitter<string> = new EventEmitter();
  @Output()
  private onDeleteDetail: EventEmitter<string> = new EventEmitter();
  @Output()
  private onAddAppointment: EventEmitter<string> = new EventEmitter();
  @Output()
  private followChange : EventEmitter<{'isFollow': boolean, 'clientID': string}> = new EventEmitter();
 
  //for extension placeholder
  @Input()
  detailPreTemplate:TemplateRef<any>;

  @Input()
  detailPostTemplate:TemplateRef<any>;


  private _customerDetail: CustomerDetail = new CustomerDetail();
  @Input() 
  get customerDetail() { return this._customerDetail;}
  set customerDetail(inputCustomerDetail) {
      //check datasource is from OPUS
      this._customerDetail = inputCustomerDetail;
      console.log('inputCustomerDetail: ', inputCustomerDetail);
      if(this._customerDetail.dataSource == 'OPUS') {
        this.isEmptyDel = true;
      }
      else {
        this.isEmptyDel = false;
      }

      this._customerDetail.updateEmptyStatus();
      this.convertEmptyValToDisplayText(this._customerDetail);
      this.checkInfoIsEmpty();
      this.changeCollateButton();
      
      this.changeDetctor.detectChanges();
  }


  public language : Language = new Language();

  // empty status
  public isEmptyAppointment: boolean = false;
  public isEmptyEdit: boolean = false;
  public isEmptyDel: boolean = false;
  public isEmptyDetailInfo: boolean = false;
  public isEmptyContactNote: boolean = false;
  public isCollapseBtnShow  : boolean = true;
  public isHasAgeRange: boolean = false;
  public isHasContactFrequancy: boolean = false;
  

  

  constructor(private dateUtils : DateUtils, private changeDetctor:ChangeDetectorRef, 
   ) {
   
   }
   

  ngOnInit() {
  }

  private changeCollateButton() {
    console.debug('changeCollateButton',this.customerDetail.tels.length,this.customerDetail.emails.length,this.customerDetail.addresses.length);
    if(this.customerDetail.tels.length <= 2 && this.customerDetail.emails.length <= 1 && this.customerDetail.addresses.length <= 1) {
      this.isCollapseBtnShow = false;
    }
    else {
      this.isCollapseBtnShow = true;
    }

    console.debug('isCollapseBtnShow',this.isCollapseBtnShow);
  }



  private checkInfoIsEmpty() {
    //if detail info is empty , display no data status      
    if(this.customerDetail.isEmptyInfo) {
      this.isEmptyDetailInfo = true;
    }
    else {
      this.isEmptyDetailInfo = false;
    }
    
  }

  private convertEmptyValToDisplayText(customerDetail : CustomerDetail) {
     //if not data set default value(?? or -- --)
     console.log("convertEmptyValToDisplayText: ", customerDetail);
     if(StringUtils.isEmpty(customerDetail.ageRange)) {
       this.isHasAgeRange = false;
     }
     else {
       this.isHasAgeRange = true;
     }
     if(StringUtils.isEmpty(customerDetail.contactFrequancy)) {
       this.isHasContactFrequancy = false;
     }
     else {
       this.isHasContactFrequancy = true;
     }
     if(StringUtils.isEmpty(customerDetail.gender)) customerDetail.gender = '- -';
     if(StringUtils.isEmpty(customerDetail.occupation)) customerDetail.occupation = '- -';
     if(StringUtils.isEmpty(customerDetail.company)) customerDetail.company = '- -';
     if(StringUtils.isEmpty(customerDetail.income)) customerDetail.income = '- -';
     if(StringUtils.isEmpty(customerDetail.source)) customerDetail.source = '- -';
     if(StringUtils.isEmpty(customerDetail.marriage)) customerDetail.marriage = '- -';
     if(StringUtils.isEmpty(customerDetail.children)) customerDetail.children = '- -';
     if(StringUtils.isEmpty(customerDetail.familiarity)) customerDetail.familiarity = '- -';
     if(StringUtils.isEmpty(customerDetail.recentStatus)) customerDetail.recentStatus = '- -';
     if(StringUtils.isEmpty(customerDetail.manpa)) customerDetail.manpa = '- -';
     if(StringUtils.isEmpty(customerDetail.children)) customerDetail.children = '- -';
  }

  public toBirthday() {
   
    if(this.customerDetail.birthday != undefined) {
      return this.dateUtils.toDateString(this.customerDetail.birthday,'yyyy-MM-dd');
    }
    else {
      return '- -';
    }
    
  }

  edit() {
    this.onEditDetail.emit(this.customerDetail.clientID);
  }

  delete() {
    this.onDeleteDetail.emit(this.customerDetail.clientID);
  }  

  callPhone() {
    this.onCallPhone.emit(this.customerDetail.clientID);
  }

  addAppointment() {
    this.onAddAppointment.emit(this.customerDetail.clientID);
  }

  trackByFn(index, item) {
    return item.name;
  }

  
  isFollowChange(isFollow : boolean) {
    console.debug('isFollowChange',isFollow);
    // display new follow state first
    this.customerDetail.updateFollowStatus(isFollow);
    this.changeDetctor.detectChanges();
   
    //ouput follow detail change status
    this.followChange.emit({'isFollow': isFollow, "clientID": this.customerDetail.clientID});
    

  }

}
