import { Component, OnInit, Output, EventEmitter, Input, ChangeDetectorRef, ElementRef, Inject, Optional, Injectable } from '@angular/core';
import { StringUtils, Language } from '@allianzSND/core';
import { CustomerItem } from '../../service/model/CustomerItem';
import { showCustomerRuleToken } from '../../injectionToken/injection-token';
import { showCustomerRule } from '../../interface/showCustomerRule.interface';

@Component({
  selector: 'app-customer-list',
  templateUrl: './customer-list.component.html',
  styleUrls: ['./customer-list.component.scss']
})
export class CustomerListComponent implements OnInit {

  //Is Default Data(No Search Status)
  public isDefaultData: boolean = true;
  public showSearchNoData: boolean = false;

  public loadingFinish: boolean = true;

  constructor(
    private changeDetecor: ChangeDetectorRef,
    private elementRef: ElementRef,
    @Optional() @Inject(showCustomerRuleToken) private showCustomerRule: showCustomerRule,
  ) { }

  @Output()
  public customerClick: EventEmitter<CustomerItem> = new EventEmitter();

  @Output()
  public customerLoad: EventEmitter<any> = new EventEmitter();

  @Output()
  public customerRefresh: EventEmitter<any> = new EventEmitter();

  @Input()
  get customerList() { return this._customerList; }
  set customerList(customerList: Array<CustomerItem>) {
    customerList.forEach(item => {
      item.showName = this.convertNameToShow(item.firstName, item.lastName);
    })
    this._customerList = customerList;

    this.loadingFinish = true;
  }
  private _customerList: Array<CustomerItem> = [];

  @Input()
  get filterType() { return this._filterType; }
  set filterType(filterType) {
    this._filterType = filterType;

    if (this.customerList.length == 0 && StringUtils.isNotEmpty(this.filterType)) {
      this.isDefaultData = this.filterType == 'NONE';
      this.showSearchNoData = this.filterType == 'SEARCH';
    }
  }
  private _filterType;

  public language: Language = new Language();
  public onClickCustomerID;


  ngOnInit() {
  }


  trackByClientID(index, item) {
    return item.clientID;
  }



  async closeSlidingItems() {
    console.debug('list closeSlidingItems');

    const item = this.elementRef.nativeElement.querySelector('ion-item-sliding');
    if (item != null) {
      /// console.debug('item size',item.length);
      console.debug('item', item);
      await item.close();
      await item.closeOpened();
    }

  }

  onClickCustomer(clientID: string) {

    this.onClickCustomerID = clientID;

    setTimeout(() => {
      this.closeSlidingItems();
    }, 300);

    let item = this.customerList.filter(item => item.clientID == clientID);

    this.customerClick.emit(item[0]);
  }


  loadCustomer(e) {
    console.log('lazy load customer:', e);

    this.customerLoad.emit();

  }

  refreshCustomer() {
    //set timeout for refresh animation
    setTimeout(() => {
        this.customerRefresh.emit();
    }, 800);
  }

  convertNameToShow(firstName: string, lastName: string): string {
    if(this.showCustomerRule) {
      return this.showCustomerRule.convertName(firstName, lastName);
    }
    else {
      return (firstName + lastName);
    }
  }
}
