import { StringUtils } from '@allianzSND/core';
import { CustomerAddress } from './CustomerAddress';
import { CustomerEmail } from './CustomerEmail';
import { CustomerTel } from './CustomerTel';
export class CustomerDetail{
    private _clientID  : string;
    private _lastName : string;
    private _firstName : string;
    private _occupation : string;
    private _company : string;
    private _birthday : Date;
    private _ageRange : string;
    private _gender : string;
    private _income : string;
    private _source : string;
    private _marriage : string;
    private _children : string;
    private _familiarity : string;
    private _recentStatus : string;
    private _manpa : string;
    private _contactFrequancy : string;
    private _possibility : string;
    private _isFollow : boolean;
    private _dataSource : string;
    private _isEmptyInfo : boolean;

    private _emails : Array<CustomerEmail> = new Array<CustomerEmail>();
    private _tels : Array<CustomerTel> = new Array<CustomerTel>();
    private _addresses : Array<CustomerAddress> = new Array<CustomerAddress>();

    constructor() {

            if(StringUtils.isEmpty(this.firstName)) this.firstName = '';
            if(StringUtils.isEmpty(this.occupation)) this.occupation = '';
            if(StringUtils.isEmpty(this.company)) this.company = '';
            if(StringUtils.isEmpty(this.ageRange)) this.ageRange = undefined;
            if(StringUtils.isEmpty(this.gender)) this.gender = undefined;
            if(StringUtils.isEmpty(this.income)) this.income = undefined;
            if(StringUtils.isEmpty(this.source)) this.source = undefined;
            if(StringUtils.isEmpty(this.marriage)) this.marriage = undefined;
            if(StringUtils.isEmpty(this.children)) this.children = undefined;
            if(StringUtils.isEmpty(this.familiarity)) this.familiarity = undefined;
            if(StringUtils.isEmpty(this.recentStatus)) this.recentStatus = undefined;
            if(StringUtils.isEmpty(this.manpa)) this.manpa = undefined;
            if(StringUtils.isEmpty(this.contactFrequancy)) this._contactFrequancy = undefined;
            if(StringUtils.isEmpty(this.possibility)) this.possibility = '';
            if(StringUtils.isEmpty(this.clientID)) this.clientID = '';

            this.updateEmptyStatus();
    }

    get dataSource() {
        return this._dataSource;
    }

    get clientID() {
        return this._clientID;
    }    

    get lastName(){
        return this._lastName;
    }

    get firstName(){
        return this._firstName;
    }

    get tels(){
        return this._tels;
    }

    get emails(){
        return this._emails;
    }

    get addresses(){
        return this._addresses;
    }

    get birthday(){
        return this._birthday;
    }

    get ageRange(){
        return this._ageRange;
    }

    get gender(){
        return this._gender;
    }

    get occupation(){
        return this._occupation;
    }

    get company(){
        return this._company;
    }

    get income(){
        return this._income;
    }

    get source(){
        return this._source;
    }

    get marriage(){
        return this._marriage;
    }

    get children(){
        return this._children;
    }

    get familiarity(){
        return this._familiarity;
    }

    get recentStatus(){
        return this._recentStatus;
    }

    get manpa(){
        return this._manpa;
    }

    get contactFrequancy(){
        return this._contactFrequancy;
    }

    get possibility(){
        return this._possibility;
    }

    get isFollow() {
        return this._isFollow;
    }

    get isEmptyInfo() {
        return this._isEmptyInfo;
    }


    set dataSource(dataSource : string) {
        this._dataSource = dataSource;
    }
    
    set clientID(clientID : string) {
        this._clientID = clientID;
    }

    set lastName(lastName : string){
        this._lastName = lastName;
    }

    set firstName(firstName : string){
        this._firstName = firstName;
    }

    set tels(tels : Array<CustomerTel>){
        this._tels  = tels;
    }

    set emails(emails : Array<CustomerEmail>){
        this._emails = emails;
    }

    set addresses(addresses : Array<CustomerAddress>){
        this._addresses = addresses;
    }

    set birthday(birthday : Date){
        this._birthday = birthday;
       
    }

    set ageRange(ageRange : string){
        this._ageRange = ageRange;
    }

    set gender(gender : string){
        this._gender = gender;
    }


    set occupation(occupation : string){
        this._occupation = occupation;
    }

    set company(company : string){
        this._company = company;
    }

    set income(income : string){
        this._income = income;
    }

    set source(source : string){
        this._source = source;
    }

    set marriage(marriage : string){
        this._marriage = marriage;
    }

    set children(children : string){
        this._children = children;
    }

    set familiarity(familiarity : string){
        this._familiarity = familiarity;
    }

    set recentStatus(recentStatus : string){
        this._recentStatus = recentStatus;
    }

    set manpa(manpa : string){
        this._manpa = manpa;
    }

    set contactFrequancy(contactFrequancy : string){
        this._contactFrequancy = contactFrequancy;
    }

    set possibility(possibility : string){
        this._possibility = possibility;   
    }

    set isFollow(isFollow : boolean) {
        this._isFollow = isFollow;
    }

    set isEmptyInfo(isEmptyInfo : boolean) {
        this._isEmptyInfo = isEmptyInfo;
    }


    updateEmptyStatus() {
        if(this.birthday == null && StringUtils.isEmpty(this.gender) && StringUtils.isEmpty(this.occupation)
          && StringUtils.isEmpty(this.company) && StringUtils.isEmpty(this.income)
          && StringUtils.isEmpty(this.source) && StringUtils.isEmpty(this.marriage)
          && StringUtils.isEmpty(this.children) && StringUtils.isEmpty(this.familiarity)
          && StringUtils.isEmpty(this.manpa) && StringUtils.isEmpty(this.recentStatus)) {
            this.isEmptyInfo = true;
          }
          else {
              this.isEmptyInfo = false;
          }
    }

    updateFollowStatus(isFollow : boolean) {
        this.isFollow = isFollow;
    }

    addEmail(email : CustomerEmail) {
        this.emails.push(email);
    }

    addTel(tel : CustomerTel) {
        this.tels.push(tel);
    }

    addAddress(address : CustomerAddress) {
        this.addresses.push(address);
    }

    deleteTel(tel:CustomerTel,index:number){
        this.tels.splice(index,1);
    }
    
    deleteEmail(email:string,index:number){
        this.emails.splice(index,1);
    }
    deleteAddress(addresses : CustomerAddress,index:number){
        this.addresses.slice(index,1);
    }

    notOPUSTelNumber() : number{
        let count = 0;

        for(let i = 0 ; i < this.tels.length ; i++) {
            if(this.tels[i].dataSource != 'OPUS') count++;
        }

        return count;
    }
}