import { IAPI, IMockAPI, DaoFactory, ClientCustomDao, ISQLiteAPI, EqualRestriction } from "@allianzSND/core";
import { Observable } from 'rxjs';

export class CustomerAddContactNoteAPI implements IAPI , IMockAPI, ISQLiteAPI {
  private clientID : string;
  private customerClientID:  string;
  private note : string;
  private noteTime : Date;

  constructor(private daoFactory: DaoFactory) {

  }
  
  setClientID(clientID : string) {
    this.clientID = clientID;
  }

  setCustomerClientID(customerClientID : string) {
    this.customerClientID = customerClientID;
  }

  setNote(note : string) {
    this.note = note;
  }

  setNoteTime(noteTime : Date) {
    this.noteTime = noteTime;
  }

  getAPIName() : string {
    return 'addCustomerContactNote';
  }

  getMockPath() : string {
    return './assets/mock/saveSuccess.json';
  }

  executeSQL(): Observable<any> {
    return Observable.create((observer) => {
      let contactObj = this.daoFactory.getDefaultTable("TW_LH_SD_Customer_Contact");
      let dao = this.daoFactory.getDefaultDao();
      if (contactObj != undefined && dao != undefined) {
        
        dao = new ClientCustomDao(dao);

        contactObj.setValue('CustomerClientID', this.customerClientID);
        contactObj.setValue('Note', this.note);
        contactObj.setValue('NoteTime', this.noteTime.getTime());
      
        if(this.clientID != undefined) {
          contactObj.addRestriction(new EqualRestriction('ClientID',[this.clientID]));
          dao.updateByTable(contactObj).subscribe((resp) => {
            observer.next(resp);
            observer.complete();
          });
        }
        else {
          dao.insertByTable(contactObj).subscribe((resp) => {
            observer.next(resp);
            observer.complete();
          });
        }

        
      } else {
        observer.next(false);
        observer.complete();
      }
    });
  }
}
