  validProfile(customerDetail : CustomerDetail): ValidationResult {
    let validationResult = new ValidationResult();

    //valid required
    if (StringUtils.isEmpty(customerDetail.lastName))
      validationResult.setErrorMap('lastName', 'required');
    // if (customerProfile.firstName == '')
    //   validationResult.setErrorMap('firstName', 'required');
    
    //valid format      

    //valid email
    if(customerDetail.emails.length != 0 ) {
      customerDetail.emails.forEach((email) => {
        if(!email.isEmpty()) {
          if(!this.isEmailFormat(email.email)) {
            validationResult.setErrorMap('email','format');
          }
        }
      });
      
    }

    //valid date
    // if(customerProfile.birthday == null) {
    //   validationResult.setErrorMap('birthday', 'date');
    // }

    return validationResult;
  }

  private isEmailFormat(email) {
    let regexp = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

    return regexp.test(email);
  }

  saveCustomerDetail(customerDetail : CustomerDetail):Observable<any> {
    console.log("saveCustomerProfile",customerDetail);

    //check tel/email/address default type
    customerDetail.tels.forEach((tel) => {
      if(StringUtils.isEmpty(tel.telType)) tel.telType = 'TelHome';
    });

    customerDetail.emails.forEach((email) => {
      if(StringUtils.isEmpty(email.emailType)) email.emailType = 'MailHome';
    });

    customerDetail.addresses.forEach((address) => {
      if(StringUtils.isEmpty(address.addressType)) address.addressType = 'AddressTypeHome';
    });

    let saveCustomerDetailAPI : CustomerSaveDetailAPI = <CustomerSaveDetailAPI>this.APIFactory.getAPI('saveCustomerDetail'); 
  
    saveCustomerDetailAPI.setDetail(customerDetail);

    let subject = Observable.create((observer)=>{
      this.dispatcher.dispatch(saveCustomerDetailAPI).subscribe((data)=>{

        console.debug('customer-service-saveCustomerDetail',data);

        observer.next(data['Header']);
        observer.complete(); 
      })
    })
    console.log("subject: ", subject);
    return subject;

  }

