import { Component, OnInit, Input, Output, EventEmitter, ElementRef, Inject } from '@angular/core';
import { DateUtils, Language} from '@allianzSND/core';
import { CustomerContactNote } from '../../service/model/CustomerContactNote';

@Component({
  selector: 'app-customer-contact-list',
  templateUrl: './customer-contact-list.component.html',
  styleUrls: ['./customer-contact-list.component.scss']
})
export class CustomerContactListComponent implements OnInit {


  public language: Language = new Language();

  @Output()
  private onAddNote: EventEmitter<any> = new EventEmitter();

  @Output()
  private onDisplayNote: EventEmitter<CustomerContactNote> = new EventEmitter();

  @Output()
  private onEditNote: EventEmitter<CustomerContactNote> = new EventEmitter();

  @Output()
  private onDeleteNote: EventEmitter<CustomerContactNote> = new EventEmitter();

  @Output()
  private contactListRefresh: EventEmitter<any> = new EventEmitter();

 


  @Input()
  get contactList() { return this._contactList; }
  set contactList(contactList) {
    
    this._contactList = contactList;
   
    setTimeout(() => {
      this.closeSlidingItems();
    }, 200)
  }
  private _contactList: Array<CustomerContactNote>;

  elementRef: ElementRef;
  constructor(private dateUtils: DateUtils,
    @Inject(ElementRef) elementRef: ElementRef) {
    this.elementRef = elementRef;
  }


  ngOnInit() {
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.closeSlidingItems();
    }, 200)

  }

  // ionic sliding need to call close event when every refresh
  async closeSlidingItems() {
    console.debug('closeSlidingItems');
  

    const item = this.elementRef.nativeElement.querySelector('ion-item-sliding');
    if (item != null) {
      /// console.debug('item size',item.length);
      console.debug('item', item);
      await item.close();
      await item.closeOpened();
    }

  }

  refreshContactNote() {
    this.contactListRefresh.emit()
   

      // need to wait ion-item-sliding create at first from no data (when skelton interface)
    setTimeout(() => {
      this.closeSlidingItems();
    }, 200)

  }

  addNote() {
    this.onAddNote.emit();
  }

  displayNote(note : CustomerContactNote) {
    this.onDisplayNote.emit(note);
  }

  editNote(note : CustomerContactNote) {
    this.onEditNote.emit(note);
  }

  deleteNote(note : CustomerContactNote) {
    this.onDeleteNote.emit(note);
  }


  trackByClientID(index, item) {
    return item.getClientID();
  }

  public toNoteTime(time: Date) {
    return this.dateUtils.toDateString(time, 'yyyy-MM-dd HH:mm');
  }
}
