import { CustomerFilterPreset } from "./customer-filter-preset";
import { StringUtils } from "@allianzSND/core";

export class CustomerFilterCriteria {
    private _keyword: string;
    private filterMap: Map<string, Array<string>> = new Map<string, Array<string>>();
    private _savePreset: boolean = false;

    constructor(keyword = '', filterMap = new Map<string, Array<string>>(), savePreset = false) {
        this._keyword = keyword;
        this.filterMap = filterMap;
        this._savePreset = savePreset;
    }

    addCriteria(filterCol: string, value: string) {
        let values: Array<string> = this.filterMap.get(filterCol);
        if (values == undefined) values = new Array<string>();

        if (!values.includes(value)) {
            values.push(value);

            this.filterMap.set(filterCol, values);
        }
    }

    addCriteriaCols(filterCol: string, values: string[]) {
        this.filterMap.set(filterCol, values);
    }

    hasCriteria() {
        return this.filterMap.size != 0 || StringUtils.isNotEmpty(this._keyword);
    }

    set keyword(keyword: string) { this._keyword = keyword }
    get keyword() { return this._keyword }

    set savePreset(isSave: boolean) {
        this._savePreset = isSave;
    }

    get savePreset() { return this._savePreset }

    getFilterMap() {
        return this.filterMap;
    }



    toPresetJSON(): CustomerFilterPreset {
        let preset = new CustomerFilterPreset();

        this.filterMap.forEach((value: Array<string>, column: string) => {
            preset.addValues(column, value);
        });

        console.debug('toPresetJSON', preset);

        return preset;
    }
}