import { ProfileCode, ProfileCodeService, SQLiteTable, StringUtils } from "@allianzSND/core";
import { SelectOption } from "@allianzSND/ui";
import { differenceInCalendarDays } from 'date-fns';
import { CustomerDetail } from "../service/model/CustomerDetail";
import { Injectable } from "@angular/core";

@Injectable({providedIn:'root'})
export class CustomerUtils {

    constructor(private profileCodeService: ProfileCodeService) { }

    setCode2Option(codeArray: Array<ProfileCode>) {
        let options: Array<SelectOption> = new Array();
        if (codeArray != undefined) {
            for (let code of codeArray) {
                options.push(new SelectOption(code.getCode(), code.displayText));
            }
        }


        return options;
    }
    
    countCompletenessByProfile(customerDetail : CustomerDetail) {
        console.debug('countCompletenessByDetail',customerDetail);

        //count Completeness
        let completeness: number = 0;
        let countColumnName = ['FirstName', 'LastName', 'Occupation', 'Company', 'AgeRange', 'Gender',
            'Income', 'Source', 'Marriage', 'Children', 'Familiarity', 'RecentStatus', 'MANPA',
            'ContactFrequancy', 'Possibility'];

            if(StringUtils.isNotEmpty(customerDetail.firstName)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.lastName)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.occupation)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.company)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.ageRange)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.gender)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.income)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.source)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.marriage)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.children)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.familiarity)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.recentStatus)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.manpa)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.contactFrequancy)) completeness++;
            if(StringUtils.isNotEmpty(customerDetail.possibility)) completeness++;

      

        //count birthday
        if (customerDetail.birthday != undefined) completeness++;

        //count phone/email/address
        if (customerDetail.tels.length != 0) completeness++;
        if (customerDetail.emails.length != 0) completeness++;
        if (customerDetail.addresses.length != 0) completeness++;

        console.debug('orginal completeness',completeness,(countColumnName.length + 4));

        completeness = (completeness / (countColumnName.length + 4));

        console.debug('before round completeness', completeness);
        completeness = Math.round(completeness * 100) / 100;
        console.debug('after round completeness', completeness);

        return completeness;
    }

    countCompleteness(customerObj : SQLiteTable,telArraySize : number,emailArraySize : number,addressAddressSize : number) {
        console.debug('countCompleteness',customerObj,telArraySize,emailArraySize,addressAddressSize);
        
        //count Completeness
        let completeness: number = 0;
        let countColumnName = ['FirstName', 'LastName', 'Occupation', 'Company', 'AgeRange', 'Gender',
            'Income', 'Source', 'Marriage', 'Children', 'Familiarity', 'RecentStatus', 'MANPA',
            'ContactFrequancy', 'Possibility'];

        for (let columnName of countColumnName) {
            if (StringUtils.isNotEmpty(customerObj.getValue(columnName))) {
                console.debug('hasData',columnName + '=>' + customerObj.getValue(columnName));
                completeness++;
            }
        }

        //count birthday
        if (StringUtils.isNotEmpty(customerObj.getValue('BirthdayYear')) &&
        StringUtils.isNotEmpty(customerObj.getValue('BirthdayMonth')) &&
        StringUtils.isNotEmpty(customerObj.getValue('BirthdayDate'))) completeness++;

        //count phone/email/address
        if (telArraySize != 0) completeness++;
        if (emailArraySize != 0) completeness++;
        if (addressAddressSize != 0) completeness++;

        console.debug('orginal completeness',completeness,(countColumnName.length + 4));

        completeness = (completeness / (countColumnName.length + 4));

        console.debug('before round completeness', completeness);
        completeness = Math.round(completeness * 100) / 100;
        console.debug('after round completeness', completeness);

        customerObj.setValue('Completeness', completeness);
    }

    setCustomerDefaultValue(dataObject: SQLiteTable) {
        
        //count age
        if (StringUtils.isNotEmpty(dataObject.getValue('BirthdayYear')) && 
        StringUtils.isNotEmpty(dataObject.getValue('BirthdayMonth')) && 
        StringUtils.isNotEmpty(dataObject.getValue('BirthdayDate'))) {
            let birthday = new Date(Number(dataObject.getValue('BirthdayYear')),
                Number(dataObject.getValue('BirthdayMonth') - 1),
                Number(dataObject.getValue('BirthdayDate')));

            let age = this.countAge(birthday);
            dataObject.setValue('age', age);

            //check age range
            if (StringUtils.isEmpty(dataObject.getValue('AgeRange'))) {
                let ageRange = this.countAgeRange(age);
                if(ageRange != undefined) {
                    dataObject.setValue('AgeRange', ageRange);
                }
            }

        }

    }

    countAgeRange(age : number) {
        console.debug('countAgeRange',age);
        let rangeResult;
        let ageRange: Array<ProfileCode> = this.profileCodeService.getCodeArray('Customer_Age');
        ageRange.forEach(profileCode => {
            let args = profileCode.getArguments();
            console.debug('customer-utils countAgeRange args',args);
            
            let obj = JSON.parse(args);
            console.debug('customer-utils countAgeRange obj',obj);
            if (age >= obj.start && age <= obj.end) {
                console.debug('match age range',profileCode.getCode());
                rangeResult = profileCode.getCode();
            }
        });

        console.debug('rangeResult',rangeResult);

        return rangeResult;
    }

    countAge(birthday : Date) : number{
        let dates = this.calCEIntervalDays(birthday, new Date());
        console.debug('dates', dates);
        let age = Math.floor(dates / 365);
        console.debug('age', age);

        return age;
    }

    /**
     * javascript
     * (EX.1060802)
     *  1060702,1060802 31
     *  1060901,1061001 30
     * @param  sDate 
     * @param  eDate 
     * @return 
     */
    calRocIntervalDays(sDate, eDate) {
        var msecPerMinute = 1000 * 60;
        var msecPerHour = msecPerMinute * 60;
        var msecPerDay = msecPerHour * 24;
        sDate = this.leftPad(sDate, 7, '0');
        eDate = this.leftPad(eDate, 7, '0');
        var begDateStr = "" + sDate.substring(3, 5) + "/" + sDate.substring(5) + "/" + (Number(sDate.substring(0, 3)) + Number(1911));
        var endDateStr = "" + eDate.substring(3, 5) + "/" + eDate.substring(5) + "/" + (Number(eDate.substring(0, 3)) + Number(1911));
        var begDate = new Date(begDateStr);
        var endDate = new Date(endDateStr);
        var interval = endDate.getTime() - begDate.getTime();
        var days = Math.floor(interval / msecPerDay);
        return days;
    }
    /**
     * javascript
     * (EX.20170801)
     *  20170702,20170802 31
     *  20170901,20171001 30
     * @param  sDate 
     * @param  eDate 
     * @return 
     */
    calCEIntervalDays(sDate, eDate) {
        // var msecPerMinute = 1000 * 60;
        // var msecPerHour = msecPerMinute * 60;
        // var msecPerDay = msecPerHour * 24;
        // var begDateStr = "" + sDate.substring(4, 6) + "/" + sDate.substring(6) + "/" + sDate.substring(0, 4);
        // var endDateStr = "" + eDate.substring(4, 6) + "/" + eDate.substring(6) + "/" + eDate.substring(0, 4);
        // var begDate = new Date(begDateStr);
        // var endDate = new Date(endDateStr);
        // var interval = endDate.getTime() - begDate.getTime();
        // var days = Math.floor(interval / msecPerDay);

        // return days;
        return differenceInCalendarDays(eDate, sDate);

    }
    /**
     * 
     * Ex:leftPad(3,3,'0')->003
     * @param  val    []
     * @param  padLen []
     * @param  padVal []
     * @return        [description]
     */
    leftPad(val, padLen, padVal) {
        if (val.toString().length < padLen) {
            for (var i = 1; i < padLen; i++) {
                val = padVal + val;
                if (val.toString().length >= padLen) {
                    break;
                }
            }
        }
        return val;
    }
}